/* Styles go here */

.token {
  height       : 150px;
  width        : 150px;
  border-radius: 50%;
  display      : inline-block;
}

.main_container {
  width : 100vw;
  height: 100vh;
  margin: auto;
}

.main_container>img {
  display   : block;
  width     : 100%;
  height    : 100%;
  object-fit: cover;
}

.main_container>.main_page_container {
  position : absolute;
  top      : 50%;
  left     : 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

* {
  box-sizing : border-box;
  font-family: 'Press Start 2P', cursive;
}

.user_balance {
  color: #fff;
}

.button_collect_wallet {
  height: 3.188rem;
  display: flex;
  align-items: center;
  background-color: rgb(255, 151, 58) !important;
  padding: 0px 1rem;
  border: none;
  box-shadow: none;
  font-size: 1rem;
  font-weight: bold;
  line-height: 1.14;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.button_collect_wallet img {
  margin-right: 1rem;
  vertical-align: middle;
  border-style: none;
  background-color: transparent;
  height: 1.938rem;
}

* {
  cursor: url('data:image/x-icon;base64,'),auto !important;
}
.home_body {
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
  height: 100vh; }
  .home_body__list_bg .bar {
    vertical-align: middle; }
  .home_body__list_bg .quantity {
    bottom: 10px;
    width: 91%; }
  .home_body__list_bg .button_copy {
    right: 0;
    bottom: -60px;
    width: 50px;
    cursor: pointer;
    height: 30px; }
  .home_body__list_bg .hero_address {
    margin-top: 15px;
    font-size: 17px;
    font-weight: 700;
    margin-left: -15px;
    font-family: 'Courier New', Courier, monospace; }
  .home_body__list_bg .detail-hero .text {
    top: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%); }
  .home_body__list_bg .detail {
    font-size: 12px;
    line-height: 21px; }
  .home_body .room_list_area {
    background-color: #fff;
    width: 1024px;
    height: 576px;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto; }
    .home_body .room_list_area .body {
      height: 80%; }
      .home_body .room_list_area .body ul {
        padding-left: 12px;
        padding-right: 12px;
        padding-top: 12px;
        margin: 0; }
        .home_body .room_list_area .body ul li {
          list-style: none;
          padding: 5px;
          margin-top: 4px;
          width: 100%;
          border-radius: 8px;
          border: 1px solid #f4f4f4;
          display: flex;
          align-items: center;
          cursor: pointer; }
          .home_body .room_list_area .body ul li div:first-child {
            margin-right: 34px; }
          .home_body .room_list_area .body ul li div:nth-child(3) {
            margin-left: auto;
            margin-right: 34px; }
    .home_body .room_list_area .footer {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      padding-right: 15px; }

@media (max-width: 1200.5px) {
  .home_body .hero_sold {
    font-size: 17px; }
  .home_body .detail-hero {
    margin-top: 13px; }
    .home_body .detail-hero .text {
      font-size: 19px; }
  .home_body .detail {
    font-size: 14px; } }

@media (max-width: 999px) {
  .home_body .bar {
    height: 59.81px;
    width: 80% !important; }
  .home_body .quantity {
    bottom: 25px; }
  .home_body .detail-hero {
    margin-top: 13px; }
    .home_body .detail-hero .text {
      font-size: 14px; }
  .home_body .detail {
    font-size: 12px; } }

@media (max-width: 768.5px) {
  .home_body .input_long {
    height: 50px !important; }
  .home_body .detail-hero {
    margin-top: 13px; }
    .home_body .detail-hero .text {
      font-size: 20px; } }

.main_page_container .fight_screen {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center top;
  width: 1054px;
  height: 610px;
  padding: 15px;
  margin: 0px; }
  .main_page_container .fight_screen .outline {
    position: absolute;
    display: flex;
    width: 100%;
    align-items: center;
    padding: 20px 0; }
    .main_page_container .fight_screen .outline .health {
      position: relative;
      width: 43.8%;
      display: flex;
      justify-content: flex-end;
      border-top: 4px solid white;
      border-left: 4px solid white;
      border-bottom: 4px solid white;
      border-right: 4px solid white; }
      .main_page_container .fight_screen .outline .health .lose_health {
        background-color: red;
        height: 30px;
        width: 100%; }
      .main_page_container .fight_screen .outline .health #playerHealth {
        position: absolute;
        background: #818cf8;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%; }
      .main_page_container .fight_screen .outline .health #enemyHealth {
        position: absolute;
        background: #818cf8;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0; }
    .main_page_container .fight_screen .outline #timer {
      background-color: black;
      width: 100px;
      height: 50px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      border: 4px solid white; }
  .main_page_container .fight_screen #displayText {
    position: absolute;
    color: white;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none; }
  .main_page_container .fight_screen #startGame {
    width: 270px;
    height: 100px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer; }
    .main_page_container .fight_screen #startGame div {
      position: relative;
      width: 100%;
      height: 100%; }
      .main_page_container .fight_screen #startGame div p {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin-top: 15%;
        text-align: center;
        font-size: 20px;
        font-weight: 600; }

.main_page_container canvas {
  z-index: 3; }

.timeStart {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 0;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  font-size: 100px;
  color: crimson;
  z-index: 100; }

.in_room_body {
  height: 80%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .in_room_body .content_left, .in_room_body .content_right {
    width: 49%;
    padding: 10px;
    margin: 10px; }
    .in_room_body .content_left div:not(.text-center), .in_room_body .content_right div:not(.text-center) {
      display: flex;
      justify-content: center; }
      .in_room_body .content_left div:not(.text-center) img, .in_room_body .content_right div:not(.text-center) img {
        width: 90%; }
  .in_room_body .vl {
    height: 100%;
    width: 5px;
    margin-right: auto;
    background-color: red;
    margin-left: auto; }

.room_detail {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 16px 0; }

